import * as React from "react"
import { Link } from "gatsby"


const Riuter = [
  {
    text: "Miras",
    url: "miras",
    badge: false,
    description:
      "A simple example of linking to another page within a Gatsby site",
  },
  {
    text: "Miras 2",
    url: "page-2",
    badge: false,
    description:
      "A simple example of linking to another page within a Gatsby site",
  },
  {
    text: "Miras 3",
    url: "page-2",
    badge: false,
    description:
      "A simple example of linking to another page within a Gatsby site",
  },
]
const Header = () => (

  <>
    <div>
      <h1>
        Welcome to <b>Gatsby!</b> <br></br>
        {Riuter.map((link, i) => (
          <React.Fragment key={link.url}>
            <Link to={link.url}>{link.text}</Link>
            {i !== Riuter.length - 1 && <> <br></br> </>}
          </React.Fragment>
        ))}
      </h1>
    </div>
  </>
)

export default Header
